.experience {
    width: 100%;
    position: relative;
    margin-bottom: 150px;
    @media (max-width: $md-5) {
        margin-bottom: 60px;
    }
    // .experience__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
        } 
    }

    // .experience__left

    &__left {
        margin-right: 40px;
        @media (max-width: $md-4) {
            margin-bottom: 60px;
        }
    }

    // .experience__main-image

    &__main-image {
        position: relative;
        width: 487px;
        height: 608px;
        @media (max-width: $md-2) {
            width: 350px;
            height: 450px;
        }
        @media (max-width: $md-6) {
          width: 290px;
          height: 350px;
        }
    }

    // .experience__block

    &__block {
        width: 230px;
        height: 216px;
        background-color: #083d6ec5;
        padding: 30px;
        border-bottom-right-radius: 100%;
        @media (max-width: $md-6) {
            padding: 20px;
            width: 150px;
            height: 150px;
        }
    }

    &__info {
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        font-size: 54px;
        color: #fff;
        max-width: 135px;
        line-height: 60px;
        @media (max-width: $md-6) {
            font-size: 40px;
            line-height: 50px;
        }
    }


    // .experience__image

    &__image {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -20;
        width: 100%;
        height: 100%;
    }

    // .experience__circle

    &__circle {
        width: 350px;
        height: 350px;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        bottom: -100px;
        right: -50px;
        z-index: 10;
        border-radius: 50%;
        @media (max-width: $md-2) {
            width: 200px;
            height: 200px;
            right: -25px;
            bottom: -50px;
        }
        @media (max-width: $md-6) {
            width: 150px;
            height: 150px;
            right: -5px;
            bottom: -45px;
        }
    }

    // .experience__logo

    &__logo {
        @include bg("contain");
        width: 231px;
        height: 100px;
        @media (max-width: $md-2) {
            width: 100px;
        }
    }

    // .experience__circle-image

    &__circle-image {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -10;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
    }

    // .experience__dots

    &__dots {
        position: absolute;
        bottom: -55px;
        left: -99px;
        z-index: -50;
        @media (max-width: $md-1 + 200px) {
            display: none;
        }
    }

    // .experience__right

    &__right {
        max-width: 445px;
    }

    // .experience__subtitle

    &__subtitle {
        font-family: "Poppins", sans-serif;
        margin-bottom: 20px;
        text-transform: uppercase;
    }

    // .experience__title

    &__title {
        margin-bottom: 50px;
        position: relative;
        &::after {
            position: absolute;
            display: block;
            content: "";
            background-color: $blueColor;
            width: 154px;
            height: 6px;
            bottom: -20px;
            left: 0;
        }
    }

    // .experience__text

    &__text {
        margin-bottom: 50px;
    }

    // .experience__button

    &__button {
        width: 223px;
        height: 56px;
    }
}
