.benifits {
    width: 100%;
    position: relative;
    margin-bottom: 150px;
    @media (max-width: $md-5) {
        margin-bottom: 60px;
    }
    // .benifits__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .benifits__header

    &__header {
        margin-bottom: 87px;
        width: 100%;
        @media (max-width: $md-4) {
            margin-bottom: 57px;
        }
    }

    // .benifits__subtitle

    &__subtitle {
        margin-bottom: 20px;
        font-family: "Poppins", sans-serif;
        text-transform: uppercase;
        letter-spacing: 2px;
        @media (max-width: $md-3) {
            margin-bottom: 0;
        } 
    }

    // .benifits__title

    &__title {
        position: relative;
        max-width: 432px;
        &::after {
            position: absolute;
            display: block;
            content: "";
            background-color: $blueColor;
            width: 183px;
            height: 6px;
            bottom: -20px;
            left: 0;
        }
    }

    // .benifits__content

    &__content {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
        }
    }

    // .benifits__item

    &__item {
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            margin-bottom: 30px;
        }
    }

    // .benifits__ico

    &__ico {
        @include bg("contain");
        width: 96px;
        height: 96px;   
        margin: 0 16px 0 0;
        @media (max-width: $md-2) {
            margin: 0 10px 0 0;
            width: 66px;
            height: 66px;
        } 
        @media (max-width: $md-4) {
            width: 100px;
            height: 100px;
        }
    }

    // .benifits__block

    &__block {
        display: flex;
        flex-direction: column;
    }

    // .benifits__caption

    &__caption {
        font-size: 32px;
        font-family: "Poppins", sans-serif;
        margin-bottom: 5px;
        font-weight: 500;
        @media (max-width: $md-3) {
            font-size: 25px;
        }
        @media (max-width: $md-4) {
            font-size: 32px;
        }
    }

    // .benifits__text

    &__text {
        max-width: 263px;
        @media (max-width: $md-3) {
            font-size: 14px;
        }
        @media (max-width: $md-4) {
            font-size: 16px;
        }
    }
}
