.kits {
    width: 100%;
    position: relative;
    margin-bottom: 150px;
    @media (max-width: $md-5) {
        margin-bottom: 60px;
    }
    // .kits__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .kits__header

    &__header {
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 100px;
        @media (max-width: $md-4) {
            margin-bottom: 70px;
        }
    }

    // .kits__title

    &__title {
    }

    // .kits__content

    &__content {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    // .kits__dots

    &__dots {
        position: absolute;
        top: -100px;
        left: -10px;
        z-index: -40;
        @media (max-width: $md-3) {
            width: 150px;
            top: -40px;
            
        }
        @media (max-width: $md-4) {
            width: 200px;
        }
    }

    // .kits__item

    &__item {
        width: 357px;
        position: relative;
        margin: 0 10px;
        height: 246px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color: #fff;
        @media (max-width: $md-4) {
            width: 80%;
            margin-bottom: 30px;
        }
    }

    // .kits__image

    &__image {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -10;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // .kits__sale

    &__sale {
        width: 80px;
        height: 32px;
        background-color: $blueColor;
        text-transform: uppercase;
        font-size: 14px;
        font-family: "Rubik", sans-serif;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 13px;
    }

    // .kits__bottom

    &__bottom {
        height: 55px;
        font-family: "Poppins", sans-serif;
        width: 100%;
        background-color: #083d6ec5;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 12px;
    }

    // .kits__text

    &__text {
        font-weight: 500;
        font-size: 19px;
        margin: 0 10px 0 0;
        @media (max-width: $md-4) {
            font-size: 16px;
        }
    }

    // .kits__price

    &__price {
        width: 96px;
        height: 40px;
        border-radius: 5px;
        border: 1px solid #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 18px;
    }
}
