.video {
    width: 100%;
    height: 600px;
    margin-bottom: 160px;
    @media (max-width: $md-5) {
        margin-bottom: 60px;
    }
    @media (max-width: $md-6) {
        height: 500px;
    }
    position: relative;

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    // .video__content

    &__content {
       

    }

    // .video__play

    &__play {
        width: 112px;
        height: 112px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #fff;
        margin-top: 100px;
        position: relative;
        cursor: pointer;
        @media (max-width: $md-6) {
            width: 70px;
            height: 70px;
        }
        img {
          width: 33px;
          height: 43px;
          margin: 0 0 0 10px;
          @media (max-width: $md-6) {
              width: 25px;
              height: 30px;
              margin: 0 0 0 7px;
          }
        }
    }

    // .video__image


    &__image {
        position: absolute;
        width: 100%;
        height: 500px;
        bottom: 0;
        left: 0;
        z-index: -49;
        object-fit: cover;
        @media (max-width: $md-6) {
            height: 400px;
        } 
    }

    &_d {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -50;
    }
}

