.header {
    width: 100%;
    height: 56px;
    position: absolute;
    margin-top: 53px;
    z-index: 50;
    @media (max-width: $md-5) {
        margin-top: 20px;
    }
   
    // .header__container

    &__container {
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }


    // .header__logo

    &__logo {
        @include bg("contain");
        width: 131px;
        height: 55px;
        cursor: pointer;
        position: relative;
        z-index: 3;
        @media (max-width: $md-4) {
            width: 90px;
         }
    }

    // .header__content

    &__content.active {
        top: 0;
    }
    &__content {
        display: flex;
        align-items: center;
        z-index: 1;
        position: relative;
        @media (max-width: $md-5) {
            position: fixed;
            width: 100%;
            height: 100%;
            background-color: $lightGreyColor;
            padding: 160px 40px;
            left: 0;
            top: -100%;
            flex-direction: column;
            align-items: center;
            transition: .4s all 0s ease;
        }
    }

    // .header__items

    &__items {
        font-family: "Poppins", sans-serif;
        margin: 0 30px 0 0;
        list-style: none;
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            margin: 0 10px 0 0;
         }
         @media (max-width: $md-5) {
             flex-direction: column;
             align-items: center;
             padding: 0;
             margin: 0;
         }
    }

    // .header__item

    &__item {
        width: 93px;
         font-weight: 600;
         font-size: 16px;
         cursor: pointer;
         position: relative;
         margin: 0 30px;
         letter-spacing: 1px;
         display: flex;
         justify-content: center;
         @media (max-width: $md-4) {
            margin: 0 5px;
         }
         @media (max-width: $md-5) {
             margin: 0 0 40px 0 ;
             font-size: 20px;
         }
         &:hover {
            color: $blueColor;
            &::before {
                position: absolute;
                display: block;
                content: "";
                width: 61px;
                height: 3px;
                background-color: $blueColor;
                bottom: -10px;
                left: 17px;
            }
          
         }
        // .header__item_active

        &_active {
            color: $blueColor;
            &::before {
                position: absolute;
                display: block;
                content: "";
                width: 61px;
                height: 3px;
                background-color: $blueColor;
                bottom: -10px;
                left: 17px;
            }
        }
    }

    // .header__button

    &__button {
        width: 195px;
        height: 56px;
        &:hover, &:focus {
            background-color: rgba(0, 0, 0, 0);
        
        }
        @media (max-width: $md-4) {
            width: 100px;
            font-size: 14px;
            height: 40px;
         }
         @media (max-width: $md-5) {
             width: 250px;
         }
    }

    &__burger.active {
        &::before {
            top: 8px;
            transform: rotate(-45deg);
        }
        &::after {
            bottom: 8px;
            transform: rotate(45deg);
        }
        span {
          display: none;
        }
    }
    &__burger {
        display: none; 
        @media (max-width: $md-5) {
            display: block;
            z-index: 3;
            position: relative;
            width: 35px;
            height: 18px;
            &::before,
            &::after {
                position: absolute;
                display: block;
                content: "";
                width: 100%;
                height: 2px;
                background-color: $blueColor;
                left: 0;
                transition: .4s all 0s ease;
            }
            &::before {
                top: 0;
            }
            &::after {
                bottom: 0;
            }
            span {
                width: 100%;
                height: 2px;
                background-color: $blueColor;
                position: absolute;
                left: 0;
                top: 8px;
            }
        }
    }
}
