.footer {
    width: 100%;
    position: relative;
    padding: 50px 0 16px 0;
    background-color: $blueColor;
    // .footer__container

    &__container {
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
        font-family: "Rubik", sans-serif;
        color: #FFF;
    }

    // .footer__main

    &__main {
        display: flex;
        justify-content: space-between;
        margin: 0 0 40px 0;
        @media (max-width: 650px) {
            flex-wrap: wrap;
            justify-content: flex-start;
        }
    }

    // .footer__column

    &__column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 10px;
        @media (max-width: 650px) {
            margin: 0 20px 10px 20px;
            width: 100%;
        }
        &_hide {
            @media (max-width: 650px) {
                display: none;
            }
        }
    }

    // .footer__caption

    &__caption {
        margin: 0 0 30px 0;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 500;
        @media (max-width: 650px) {
            margin: 0 0 20px 0;
            cursor: pointer;
        }
    }

    // .footer__items

    &__block {
       
    }

    &__items {
        display: flex;
        flex-direction: column;
        padding: 0;
        
    }

    // .footer__item

    &__item {
        list-style: none;
        margin: 0 0 20px 0;
        
        a {
            font-size: 16px;
            text-decoration: none;
            color: #fff;
            &:hover, &:focus {
                border-bottom: 2px solid #fff;
                padding-bottom: 2px;

            }
        }
    }

    // .footer__text

    &__text {
        max-width: 200px;
        font-size: 15px;
        margin: 0 0 50px 0;
        
        @media (max-width: 650px) {
            margin: 0 0 20px 0;
        }
    }

    // .footer__links

    &__links {
        display: flex;
        align-items: center;
    }

    // .footer__link

    &__link {
        @include bg("contain");
        width: 27px;
        height: 25px;
        margin: 0 15px;
        cursor: pointer;
        @media (max-width: $md-4) {
           margin: 5px;
        }
        @media (max-width: 650px) {
            margin: 0 20px 0 0;
        }
    }

    // .footer__bottom

    &__bottom {
        display: flex;
        justify-content: flex-end; 
        padding-top: 20px;
        border-top: 1px solid $greyColor;
        @media (max-width: 650px) {
            justify-content: flex-start;
        }
    }

    // .footer__ico

    &__ico {
        margin: 0 10px;
        @include bg("contain");
        width: 38px;
        height: 24px;
        cursor: pointer;
        @media (max-width: $md-6) {
            margin: 0 5px;
        }
    }
}
