.first-screen {
    width: 100%;
    height: 100vh;
    position: relative;
    padding-top: 150px;
    margin-bottom: 150px;
    @media (max-width: $md-5) {
        padding-top: 100px;
        margin-bottom: 60px;
    }
    @media (max-height: 425px) and (min-width: 600px) {
        height: auto;
    }
    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        z-index: 5;
        position: relative;
        @media (max-width: $md-5) {
            align-items: flex-start;
        }
    }

    // .first-screen__content

    &__content {
    }

    // .first-screen__title

    &__title {
        max-width: 452px;
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        font-size: 60px;
        margin-bottom: 5px;
        text-transform: uppercase;
        letter-spacing: 4px;
    }

    // .first-screen__text

    &__text {
        max-width: 446px;
        margin-bottom: 23px;
    }

    // .first-screen__form

    &__form {
        max-width: 440px;
        display: flex;
        align-items: center;
        height: 51px;
        background-color: #F1F1F1;
        justify-content: space-between;
        @media (max-width: 350px) {
            flex-direction: column;
            justify-content: center;
            height: auto;
            padding: 10px 0 0 0;
        }
    }

    // .first-screen__input

    &__input {
        width: 65%;
        padding: 0 20px;
        border: 1px solid transparent;
        background-color: transparent;
        font-family: "Rubik", sans-serif;
        outline: none;
        @media (max-width: 350px) {
            width: 100%;
            padding: 0 10px;
            margin-bottom: 10px;
        }
    }

    // .first-screen__button

    &__button {
        width: 138px;
        height: 100%;
        @media (max-width: 350px) {
            width: 100%;
            height: 40px;
        }
    }

    // .first-screen__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        @media (max-width: $md-5) {
            display: none;
        }
        @media (max-height: 425px) and (min-width: 600px) {
            display: none;
        }
    }
}
