*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$container: 1110px + 30px;
$md-1: $container;
$md-2: 1039.98px;
$md-3: 991.98px;
$md-4: 767.98px;
$md-5: 479.98px;
$md-6: 399.98px;
 
$blueColor: #083C6E;
$lightBlueColor: #E0EDFF;
$greyColor: #C4C4C4;
$lightGreyColor: #E9E9E9;


// Elemements
.body.lock {
	overflow: hidden;
}

.container {
	max-width: $container;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.text {
	font-size: 18px;
	font-family: "Rubik", sans-serif;
	color: #747DA1;
}

.title-1, .title-2 {
	font-weight: 500;
	font-family: "Poppins", sans-serif;
}

.title-1 {
	font-size: 52px;
}

.title-2 {
	font-size: 38px;
}

.button {
	@include no-btn();
	background-color: $blueColor;
	color: #fff;
	font-size: 18px;
	font-weight: 500;  
	font-family: "Rubik", sans-serif;
	text-transform: uppercase;
	border: 2px solid rgba(0, 0, 0, 0);
	transition: .3s all 0s linear;
	&:hover, &:focus {
		background-color: #fff;
		color: $blueColor;
		border: 2px solid $blueColor;
	}
}
