.furniture {
    width: 100%;
    position: relative;
    margin-bottom: 110px;
    @media (max-width: $md-5) {
        margin-bottom: 40px;
    }
    // .furniture__body

    &__container {
        position: relative;
    }

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
        }
    }

    // .furniture__left

    &__left {
        @media (max-width: $md-4) {
            margin-bottom: 50px;
        }
    }

    // .furniture__subtitle

    &__subtitle {
        font-family: "Poppins", sans-serif;
        text-transform: uppercase;
        letter-spacing: 2px;
        margin-bottom: 20px;
        @media (max-width: $md-3) {
            margin-bottom: 0;
        } 
    }

    // .furniture__title

    &__title {
        margin-bottom: 50px;
        position: relative;
        @media (max-width: $md-3) {
            margin-bottom: 30px;
        } 
        &::after {
            @media (max-width: $md-3) {
                bottom: -10px;
            }
            position: absolute;
            display: block;
            content: "";
            background-color: $blueColor;
            width: 154px;
            height: 6px;
            bottom: -20px;
            left: 0;
        }
    }

    // .furniture__text

    &__text {
        max-width: 445px;
        margin-bottom: 50px;
        @media (max-width: $md-3) {
            font-size: 16px;
            max-width: 350px;
            margin-bottom: 30px;
        }
    }

    // .furniture__button

    &__button {
        width: 223px;
        height: 56px;
    }

    // .furniture__image

    &__image {
        width: 448px;
        height: 362px;
        position: relative;
        @media (max-width: $md-3) {
            width: 350px;
            height: auto;
        }
        @media (max-width: $md-4) {
            width: 90%;
        }
    }

    &__dots {
        position: absolute;
        right: -55px;
        bottom: -60px;
        z-index: -50;
        @media (max-width: 1300px) {
            display: none;
        }
    }
}
