.support {
    width: 100%;
    color: $blueColor;
    // .support__top

    &__top {
        width: 100%;
        height: 155px;
    }

    // .support__header

    &__header {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
    }

    // .support__title

    &__title {
        margin-bottom: 20px;
        text-align: center;
        position: relative;
       
    }

    &__line {
        width: 154px;
        height: 6px;
        background-color: $blueColor;
        margin-bottom: 25px;
    }

    // .support__bottom

    &__bottom {
        width: 100%;
        background-color: $blueColor;
        padding-bottom: 56px;
        @media (max-width: $md-4) {
            background-color: #fff;
            padding-bottom: 0;
        }
    }

    // .support__main

    &__main {
        width: 100%;
        height: 100%;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 116px 85px 116px;
        @media (max-width: $md-1) {
            padding: 0 60px 85px 60px;
        }
        @media (max-width: $md-4) {
            padding: 0 30px 55px 30px;
        }
        @media (max-width: $md-6) {
            padding: 0 10px 55px 10px;
        }
        
    }

    // .support__text

    &__text {
        max-width: 585px;
        margin-bottom: 40px;
        text-align: center;
        @media (max-width: $md-4) {
            margin-top: 10px;
        }
    }

    // .support__form

    &__form {
        width: 100%;
        height: 73px;
        border: 1px solid $blueColor;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 8px 0 30px;
        @media (max-width: 600px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: auto;
            padding: 10px 0 0 0 ;
        }

    }

    // .support__input

    &__input {
        outline: none;
        border: 1px solid transparent;
        font-family: "Rubik", sans-serif;
        font-size: 17px;
        width: 70%;
        @media (max-width: 600px) {
            width: 100%;
            padding: 0 15px;
            text-align: center;
            height: 50px;
            margin-bottom: 10px;
        }
        @media (max-width: $md-6) {
            padding: 0;
        }
        
    }
    &__input:-ms-input-placeholder {text-transform: uppercase;}
    &__input::-ms-input-placeholder {text-transform: uppercase;}
    &__input::-webkit-input-placeholder {text-transform: uppercase;}
    &__input:-moz-placeholder {text-transform: uppercase;}
    &__input::-moz-placeholde {text-transform: uppercase;}

    // .support__button

    &__button {
        width: 209px;
        height: 56px;
        @media (max-width: 600px) {
            width: 100%;
        }
    }
}

